.Main-Pressable-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #c800cb3d;;
  cursor: pointer;
}

.Icon-Wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  background-color: rgb(68, 226, 68);
}

.Icon-Center {
  font-size: 24px;
  color: white;
  background-color:rgb(68, 226, 68);
}